.ulTabsWrapper {
    list-style-type: none;
}

.liTabs {
    display: inline;
    cursor: pointer;
    color: cornsilk;
    font-weight: 1000;
    font-family: 'Roboto', sans-serif;
    padding: 6%;
}

.liTabs:hover {
    font-size: 1.1rem;
    transition: 0.3s;
    color: darkblue;
}